.clipboard {
    margin-top:18.6rem;
    
      @media screen and(min-width:823px) {
        margin-top:5.7rem;
      }
    &__acces {
        max-width: 95.6rem;
        text-align: center;
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        padding-left:clamp(3.2rem, 5vw, 31.4rem); 
        padding-right:clamp(3.2rem, 5vw, 31.4rem);

        p {
           max-width: 73rem;
           padding-bottom: 11rem;
           margin-left: auto;
           margin-right: auto;
        }

        h2 {
           max-width: 73rem;
           text-align: center;
           margin-left: auto;
           margin-right: auto;

           @media screen and(min-width:823px) {
                margin-bottom:1.9rem;
           }
        }
   
        
    }




}



    