:root {
  --white:hsl(0,0%,100%);
  --bg-white: hsl(0,0%,94.9%);
  --black: hsl(0, 0%, 0%);
  --btn-cyan: hsl(170.7,66.2%,44.1%);
  --btn-lightblue: hsl(232.8,100%,69%);
  --bg-gray:hsl(0,0%,59.2%);
  --darkgrayishblue: hsl(210, 10%, 33%);
  --p-gray: hsl(201, 11%, 66%);
  --hli-col:hsl(210,9.5%,32.9%);
}

:root {
  --font-baijamjuree: "Bai Jamjuree", sans-serif ;
}

html {
  /*Percentage of user's browser font-size setting*/
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-baijamjuree);
  position: relative;
}

img {
  max-width: 100%;
}

*:focus {
  outline: 0.3rem dashed #0a0808;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

li {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin-top: 0;
  color: var(--hli-col);
}

h1 {
  font-size: clamp(3.2rem, 4vw, 4.6rem);
  font-weight: normal;
  letter-spacing: -0.052rem;
}
@media screen and (min-width: 900px) {
  h1 {
    letter-spacing: -0.075rem;
  }
}

h2 {
  font-size: clamp(2.8rem, 4vw, 3.6rem);
  letter-spacing: -0.046rem;
  font-weight: normal;
  margin-bottom: 1.6rem;
}
@media screen and (min-width: 900px) {
  h2 {
    letter-spacing: -0.059rem;
  }
}

h3 {
  font-size: 2.4rem;
  letter-spacing: -0.039rem;
  font-weight: normal;
  margin-bottom: 0;
  padding-bottom: 1rem;
}

p {
  margin: 0;
  font-size: clamp(1.6rem, 1.5vw, 2rem);
  color: var(--p-gray);
  line-height: 2.6rem;
  letter-spacing: 0.012rem;
}
@media screen and (min-width: 823px) {
  p {
    line-height: 3rem;
    letter-spacing: 0.015rem;
  }
}

li {
  color: var(--hli-col);
}

.container {
  margin: 0 auto;
}

.pad_b-sm {
  padding-bottom: 4.8rem;
  max-width: 50ch;
}

.fnt-p {
  font-size: clamp(1.6rem, 1.5vw, 1.8rem);
}

.fnt-p-sm {
  font-size: 1.6rem;
  letter-spacing: 0.012rem;
  line-height: 2.6rem;
}
@media screen and (min-width: 823px) {
  .fnt-p-sm {
    line-height: 3rem;
    text-align: left;
  }
}

.svg-paddbtm {
  margin-bottom: 4.4rem;
}
@media screen and (min-width: 823px) {
  .svg-paddbtm {
    margin-bottom: 0;
  }
}

button {
  display: inline-block;
  font-size: 1.8rem;
  color: var(--white);
  padding: 1.13rem 8rem;
  line-height: 3rem;
  width: 31.1rem;
  border-radius: 30rem;
  cursor: pointer;
  border: 0;
  font-weight: 400;
  transition: all 0.5s;
  margin-bottom: 2.4rem;
}
button:hover {
  filter: drop-shadow(0 0.4rem 0.4rem #333333);
  transform: translateY(-1.1rem);
}
@media screen and (min-width: 790px) {
  button {
    padding: 1.13rem 3.7rem;
    width: 22.7rem;
  }
}

.btn_cyan {
  margin-top: 4.8rem;
  background-color: var(--btn-cyan);
  border-bottom: 0.3rem solid #1d907f;
  box-shadow: inset 0 8px 20px 0px rgba(38, 186, 164, 0.4);
}
@media screen and (min-width: 705px) {
  .btn_cyan {
    margin-right: 1.11vw;
  }
}

.btn_lightblue {
  background-color: var(--btn-lightblue);
  border-bottom: 3px solid #475dff;
  box-shadow: 0 8px 20px rgba(97, 115, 255, 0.4);
}

.header {
  background-image: url("../../../images/bg-header-mobile.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: -17.8rem;
  background-position-y: 0;
  width: 100%;
}
@media screen and (min-width: 523px) {
  .header {
    background-position-x: 1.8rem;
  }
}
@media screen and (min-width: 800px) {
  .header {
    background-position-x: 0rem;
    background-position-x: 3rem;
    height: 36.3rem;
    background-image: url("../../../images/bg-header-desktop.png");
  }
}
.header__hero {
  max-width: 79rem;
  display: grid;
  place-items: center;
  text-align: center;
  padding: 13.3rem 3.2rem 0 3.2rem;
}
@media screen and (min-width: 1300px) {
  .header__hero {
    padding-top: 12.8rem;
  }
}
.header__hero-heading {
  padding-top: 6.4rem;
}
@media screen and (min-width: 1300px) {
  .header__hero-heading {
    padding-top: 5.6rem;
  }
}
.header__hero-par {
  padding-top: 1.6rem;
}
@media screen and (min-width: 593px) and (max-width: 1300px) {
  .header__hero-par {
    width: 75%;
  }
}
.header__btns {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 800px) {
  .header__btns {
    display: inline-block;
  }
}

.info {
  margin-top: 16rem;
}
.info__wrapper {
  display: grid;
  grid-template-columns: 1fr;
  place-items: center;
  padding: 0 3.2rem;
  text-align: center;
  width: 100%;
  max-width: 124rem;
  margin-top: 13rem;
}
@media screen and (min-width: 800px) {
  .info__wrapper {
    margin-top: 34rem;
  }
}
.info__wrapper img {
  padding-top: clamp(6.5rem, 9vw, 8rem);
  padding-bottom: clamp(5.6rem, 11%, 15rem);
}
@media screen and (min-width: 823px) {
  .info__wrapper img {
    position: relative;
    left: -23rem;
  }
}
@media screen and (min-width: 823px) {
  .info__wrapper {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr auto;
  }
  .info__wrapper .info__h {
    grid-column: 1/5;
    grid-row: 1/2;
    width: 62.08%;
  }
  .info__wrapper .info__p {
    grid-column: 1/5;
    grid-row: 2/3;
    width: 62.08%;
  }
  .info__wrapper .info__image {
    grid-column: 1/4;
    grid-row: 3/4;
  }
  .info__wrapper .subgrid {
    grid-column: 3/5;
    grid-row: 2/4;
    max-width: 35rem;
    text-align: left;
  }
}

.clipboard {
  margin-top: 18.6rem;
}
@media screen and (min-width: 823px) {
  .clipboard {
    margin-top: 5.7rem;
  }
}
.clipboard__acces {
  max-width: 95.6rem;
  text-align: center;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  padding-left: clamp(3.2rem, 5vw, 31.4rem);
  padding-right: clamp(3.2rem, 5vw, 31.4rem);
}
.clipboard__acces p {
  max-width: 73rem;
  padding-bottom: 11rem;
  margin-left: auto;
  margin-right: auto;
}
.clipboard__acces h2 {
  max-width: 73rem;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}
@media screen and (min-width: 823px) {
  .clipboard__acces h2 {
    margin-bottom: 1.9rem;
  }
}

.workflow {
  margin-top: 13rem;
  padding-left: 3.2rem;
  padding-right: 3.2rem;
}
.workflow__wrapper {
  max-width: 111rem;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
  column-gap: 3rem;
  justify-items: center;
}
.workflow__wrapper p {
  max-width: 40rem;
}
@media screen and (min-width: 800px) {
  .workflow__wrapper {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, auto);
  }
}
.workflow__wrapper .wrapper-h {
  margin-bottom: clamp(1.1rem, 2vw, 1.9rem);
  font-size: clamp(2.6rem, 9vw, 3.6rem);
}
@media screen and (min-width: 800px) {
  .workflow__wrapper .wrapper-h {
    grid-column: 1/4;
  }
}
.workflow__wrapper .wrapper-p {
  margin-bottom: 9rem;
  font-size: 1.6rem;
}
@media screen and (min-width: 800px) {
  .workflow__wrapper .wrapper-p {
    grid-column: 1/4;
    margin-bottom: 7.2rem;
    font-size: 1.6rem;
  }
}
.workflow .blacklists,
.workflow .snippets {
  margin-bottom: 5.6rem;
}
@media screen and (min-width: 800px) {
  .workflow .blacklists,
.workflow .snippets,
.workflow .sneak {
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    flex: 1;
    gap: 2rem;
  }
  .workflow .p-sm {
    font-size: 1.6rem;
    letter-spacing: 0.0012rem;
    line-height: 2.6rem;
  }
}
@media screen and (min-width: 800px) and (min-width: 1200px) {
  .workflow .p-sm {
    line-height: 3rem;
  }
}

.partners {
  margin-top: 13.2rem;
  margin-bottom: 15.4rem;
  padding-left: 11.8rem;
  padding-right: 11.8rem;
}
.partners__container {
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  gap: 6rem;
}
@media (min-width: 1000px) {
  .partners__container {
    max-width: 111.1rem;
    margin: 0 auto;
    gap: 3rem;
    justify-content: space-between;
  }
}
.partners__link {
  max-width: 20rem;
  width: 100%;
  /* margin: 0 auto; */
}
@media (min-width: 1000px) {
  .partners__link {
    width: initial;
    margin: 0;
  }
}
.partners__link img {
  display: block;
  margin: 0 auto;
}
.partners__link:focus {
  outline: 1px dashed rgba(255, 255, 255, 0.2);
  outline-offset: 5px;
}

.cta {
  margin-bottom: 15rem;
  padding-left: 3.2rem;
  padding-right: 3.2rem;
}
.cta__wrapp {
  max-width: 73rem;
  flex-flow: column;
  align-items: center;
  text-align: center;
}
.cta_h {
  margin-bottom: 1.9rem;
}
.cta__cta_p {
  margin-bottom: 4.8rem;
  max-width: 97%;
}

.footer__wrapp {
  max-width: 111.1rem;
  display: grid;
  grid-template-columns: 1fr;
  place-items: center;
  text-align: center;
  align-items: center;
}
.footer__wrapp .item-a {
  grid-area: logo;
}
.footer__wrapp .item-b {
  grid-area: nav;
}
.footer__wrapp .item-c {
  grid-area: socials;
}
@media screen and (min-width: 700px) {
  .footer__wrapp {
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: auto;
  }
}
.footer__wrapp img {
  width: 5.5rem;
  margin-bottom: 4.5rem;
  display: block;
}
.footer a {
  font-size: 1.8rem;
  color: var(--hli-col);
}
.footer__nav-list {
  padding: 0;
}
.footer .footer__nav-item {
  margin-bottom: 2rem;
}
.footer .footer__socials {
  display: flex;
  padding: 0;
  gap: 4rem;
  justify-content: space-between;
}